* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    background:RGBA(228, 241, 254, 1);
    font-size: 20px;
    font-family: 'Quicksand', sans-serif;
    height: 100%;
    /* display: none; */
}

#todo-list {
    margin: 70px auto;
    padding: 35px 50px 50px;
    max-width: 500px;
    border-radius: 15px;
    background:RGBA(52, 45, 113, 1);
    color: #FFF;
    box-shadow: -20px -20px 0px 0px rgb(100 100 100 / 10%);
    transition: 1s linear;
    /* display: none; */
}

#todo-list h1 {
    display: block;
    font-weight: normal;
    font-size: 40px;
    letter-spacing: 0.05em;
    border-bottom: 1px solid #fff;
    
}

#todo-list ul {
    margin-top: 30px;
    list-style: none;
}

#todo-list h1 span {
    display: block;
    font-size: 16px;
    margin-bottom: 12px;
    margin-left: 3px;
    margin-top: 4px;
    
}
li{
    cursor: pointer;
    font-size: 1.5rem;    box-shadow: 0px 0px 50px rgba(0,0,0,0.5);
    border-radius: 30px;
    margin: 5px;

}
.y,.t{
    transition: 0.5s linear;

}

li .y:hover{
    color: yellow;

}
li .t:hover{
    color:red;

}

section {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
}

section label {
 
    min-width: 100%;
    margin-bottom: 10px;
    font-size: 20px;
}

section input,
section button {
    font-family: 'Quicksand', sans-serif;
    height: 50px;

}

section input {
    flex-grow: 1;
    border: none;
    background:rgba(246, 43%, 31%);
    padding: 0 24px;
    font-size: initial;
    transition: 1s linear ;
}

input:hover{
    color: white;
      background:RGBA(52, 45, 113, 1);

}

section button {
    background:RGBA(52, 45, 113, 1);
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2rem;
    padding: 0 24px;
    border-radius: 18px;
    border: none;
    color: white;
    border: 1px solid rgba(255,255,255,.3);
    margin-left: 5px;
    cursor: pointer;
}

button {
    padding: 10px 10px;
    transition: 1s linear ;

}
button:hover{
    color:RGBA(52, 45, 113, 1);
    background-color: wheat;

}

#list li {
    display: flex;
    margin: 0 -37px 4px;
    padding: 20px 52px;
    justify-content: space-between;
    align-items: center;
    background: rgba(255,255,255,0.1);
}

#list li.done {
    text-decoration: line-through;
}

.togglebutton-wrapper {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.togglebutton-label {
    padding-right: 15px;
}

.todo-sort {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 24px;
}

.todo-sort input { 
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 4px;
    bottom: 4px;
    background-color: #FF6666;
    transition: 0.4s;
}
  
input:checked + .slider {
    background-color: #fff;
}
  
input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}
  
input:checked + .slider:before {
    transform: translateX(16px);
}

.slider.round {
    border-radius: 34px;
}
  
.slider.round:before {
    border-radius: 50%;
}



